import * as constants from './constants';

const dictionaryPending = () => ({ type: constants.DICTIONARY_PENDING });
const getCurrenciesDictionarySuccess = (list = []) => ({ type: constants.GET_CURRENCIES_DICTIONARY_SUCCESS, list });
const getStatusesDictionarySuccess = (list = []) => ({ type: constants.GET_STATUSES_DICTIONARY_SUCCESS, list });
const getClientLegalFormsDictionarySuccess = (list = []) => ({ type: constants.GET_CLIENT_LEGAL_FORMS_DICTIONARY_SUCCESS, list });
const dictionaryError = (error = '') => ({ type: constants.DICTIONARY_ERROR, error });

export {
    dictionaryPending,
    getCurrenciesDictionarySuccess,
    dictionaryError,
    getClientLegalFormsDictionarySuccess,
    getStatusesDictionarySuccess
};
