import React from 'react';
import { Box, Toolbar, Button, Divider, Tooltip } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';

const EnhancedTableToolbar = (props) => {
    const { numSelected, children } = props;

    const editTitle = numSelected < 1 || numSelected > 1 ? `Выберите один флажок` : ``;
    const deleteTitle = numSelected < 1 ? `Выберите флаж(ок)ки` : ``;

    return (
        <Toolbar
            sx={{
                pl: { sm: 2 },
                pr: { xs: 1, sm: 3 },
                pt: { sm: 2 },
                pb: { sm: 2 }
            }}
            style={{ display: 'flex', alignItems: 'flex-start' }}
        >
            <Box
                sx={{
                    width: 'calc(100% - 300px)',
                    marginRight: '30px'
                }}
            >
                {children}
            </Box>
            <Box
                sx={{
                    width: '300px',
                    display: 'flex',
                    alignItems: 'flex-start',
                    justifyContent: 'space-between'
                }}
            >
                <Tooltip title={editTitle}>
                    <Box>
                        <Button variant="contained" startIcon={<EditIcon />} disabled={numSelected < 1 || numSelected > 1}>
                            Изменить
                        </Button>
                    </Box>
                </Tooltip>
                <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
                <Tooltip title={deleteTitle}>
                    <Box>
                        <Button variant="contained" startIcon={<DeleteIcon />} disabled={numSelected < 1}>
                            Удалить
                        </Button>
                    </Box>
                </Tooltip>
            </Box>
        </Toolbar>
    );
};

export default EnhancedTableToolbar;
