import request from '@/core/utils/request';
import {
    dictionaryPending,
    getCurrenciesDictionarySuccess,
    getClientLegalFormsDictionarySuccess,
    getStatusesDictionarySuccess,
    dictionaryError
} from '@/core/store/dictionaries/actions';

function requestHelper(dispatch, endpoint, getSuccessDictionary, error) {
    return request
        .post(endpoint, {})
        .then((res) => res.json())
        .then(
            (data) => dispatch(getSuccessDictionary(data)),
            () => dispatch(dictionaryError(error))
        );
}

const getDictionaries = () => (dispatch) => {
    dispatch(dictionaryPending());
    Promise.all([
        requestHelper(dispatch, `/api/Dictionary/currencies`, getCurrenciesDictionarySuccess, 'Ошибка при загрузке списка валют'),
        requestHelper(dispatch, `/api/Dictionary/clientLegalForms`, getClientLegalFormsDictionarySuccess, 'Ошибка при загрузке правовых форм'),
        requestHelper(dispatch, `/api/Dictionary/statuses`, getStatusesDictionarySuccess, 'Ошибка при загрузке статусов')
    ]);
};

export { getDictionaries };
