import * as constants from './constants';

export const initialState = {
    pending: false,
    clientList: {},
    loanAgreementList: {},
    error: null
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case constants.LIST_PENDING:
            return {
                ...state,
                pending: true
            };
        case constants.GET_CLIENT_LIST_SUCCESS:
            return {
                ...state,
                pending: false,
                clientList: action.list
            };
        case constants.GET_LOAN_AGREEMENT_LIST_SUCCESS:
            return {
                ...state,
                pending: false,
                loanAgreementList: action.list
            };
        case constants.LIST_ERROR:
            return {
                ...state,
                pending: false,
                error: action.error
            };
        default:
            return state;
    }
};

export default reducer;
