import React, { lazy } from 'react';
import { Route, Routes } from 'react-router';
import { MainLayout } from '@/containers';
import { Login } from '@/pages';
import { Loadable } from '@/components';

const ClientList = Loadable(lazy(() => import(/* clientList */ '@/views/clientList')));
const CreateClient = Loadable(lazy(() => import(/* clientList */ '@/views/createClient')));
const LoanAgreementList = Loadable(lazy(() => import(/* loanAgreementList */ '@/views/loanAgreementList')));
const CreateLoanAgreement = Loadable(lazy(() => import(/* loanAgreementList */ '@/views/createLoanAgreement')));

const MainRoutes = () => (
    <Routes>
        <Route path="/" element={<MainLayout />}>
            <Route index element={<ClientList />} />
            <Route path="clients" element={<ClientList />} />
            <Route path="clients/create" element={<CreateClient />} />
            <Route path="contracts/loan-agreements" element={<LoanAgreementList />} />
            <Route path="contracts/loan-agreement/create" element={<CreateLoanAgreement />} />
        </Route>
        <Route path="/login" element={<Login />} />
    </Routes>
);

export default MainRoutes;
