import * as constants from './constants';

export const initialState = {
    pending: false,
    currencies: [],
    clientLegalForms: [],
    statuses: [],
    error: null
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case constants.DICTIONARY_PENDING:
            return {
                ...state,
                pending: true
            };
        case constants.GET_CURRENCIES_DICTIONARY_SUCCESS:
            return {
                ...state,
                pending: false,
                currencies: action.list
            };
        case constants.GET_CLIENT_LEGAL_FORMS_DICTIONARY_SUCCESS:
            return {
                ...state,
                pending: false,
                clientLegalForms: action.list
            };
        case constants.GET_STATUSES_DICTIONARY_SUCCESS:
            return {
                ...state,
                pending: false,
                statuses: action.list
            };
        case constants.DICTIONARY_ERROR:
            return {
                ...state,
                pending: false,
                error: action.error
            };
        default:
            return state;
    }
};

export default reducer;
