import React, { useState } from 'react';
import { Box, Tab, Divider } from '@mui/material';
import { TabContext, TabList, TabPanel } from '@mui/lab';
import { tabsClasses } from '@mui/material/Tabs';

function CustomTabs(props) {
    const { tabs } = props;
    const [value, setValue] = useState('0');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    return (
        <>
            <Box sx={{ bgcolor: 'background.paper', borderRadius: '4px' }}>
                <TabContext value={value}>
                    <TabList
                        onChange={handleChange}
                        variant="scrollable"
                        scrollButtons
                        aria-label="visible arrows tabs"
                        sx={{
                            [`& .${tabsClasses.scrollButtons}`]: {
                                '&.Mui-disabled': { opacity: 0.3 }
                            }
                        }}
                    >
                        {tabs.map((tab, index) => (
                            <Tab key={tab.tabName} label={tab.tabName} value={String(index)} />
                        ))}
                    </TabList>
                    <Divider />
                    {tabs.map((tab, index) => (
                        <TabPanel key={tab.tabName} value={String(index)}>
                            {tab.tabContent}
                        </TabPanel>
                    ))}
                </TabContext>
            </Box>
        </>
    );
}

export default CustomTabs;
