import React, { useState } from 'react';
import { Box, TextField, InputLabel, MenuItem, FormControl, Select, Checkbox } from '@mui/material';
import { DatePicker } from '@/components';

function FormCustomerInfo(props) {
    const { infoStates, infoHandlers, infoErrors } = props;
    const { legalForm, listLegalForms, iin, gender, surname, firstname, lastname, adress } = infoStates;

    return (
        <Box
            component="form"
            sx={{
                '& .MuiTextField-root': { mr: 5, mt: 2, mb: 2, width: '38ch' }
            }}
            noValidate
            autoComplete="off"
        >
            <div>
                <FormControl className="MuiTextField-root">
                    <InputLabel id="select-legal-form">Правовая форма</InputLabel>
                    <Select
                        labelId="select-legal-form"
                        id="select-legal-form"
                        value={legalForm}
                        label="Правовая форма"
                        onChange={infoHandlers.handleLegalform}
                    >
                        {listLegalForms.map((item) => (
                            <MenuItem key={item.id} value={item.code}>
                                {item.name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <FormControl className="MuiTextField-root">
                    <Box>
                        <Checkbox defaultChecked sx={{ '& .MuiSvgIcon-root': { fontSize: 28 } }} />
                        Резидент РК
                    </Box>
                </FormControl>
            </div>
            <div>
                <TextField id="customer-iin" {...infoErrors.checkIin} label="ИИН" value={iin} onChange={infoHandlers.handleIin} />
                <FormControl className="MuiTextField-root">
                    <InputLabel id="form-select-gender">Пол</InputLabel>
                    <Select
                        labelId="form-select-gender"
                        id="form-select-gender"
                        value={gender}
                        label="Пол"
                        onChange={infoHandlers.handleGender}
                    >
                        <MenuItem value="man">Мужской</MenuItem>
                        <MenuItem value="woman">Женский</MenuItem>
                    </Select>
                </FormControl>
                <DatePicker />
            </div>
            <div>
                <TextField
                    id="customer-surname"
                    {...infoErrors.checkRuString}
                    label="Фамилия"
                    value={surname}
                    onChange={infoHandlers.handleSurname}
                />
                <TextField
                    id="customer-firstname"
                    {...infoErrors.checkRuString}
                    label="Имя"
                    value={firstname}
                    onChange={infoHandlers.handleFirstname}
                />
                <TextField
                    id="customer-lastname"
                    {...infoErrors.checkRuString}
                    label="Отчество"
                    value={lastname}
                    onChange={infoHandlers.handleLastname}
                />
            </div>
            <div>
                <TextField
                    style={{ width: '81ch' }}
                    id="customer-adress"
                    {...infoErrors.checkRuString}
                    label="Адрес"
                    value={adress}
                    onChange={infoHandlers.handleAdress}
                />
            </div>
        </Box>
    );
}

export default FormCustomerInfo;
