import React, { useState } from 'react';
import { visuallyHidden } from '@mui/utils';
import { Box, TableCell, TableHead, TableRow, TableSortLabel, Paper, Checkbox, IconButton, InputBase, Divider } from '@mui/material';
import CancelPresentationIcon from '@mui/icons-material/CancelPresentation';
import SearchIcon from '@mui/icons-material/Search';

function EnhancedTableHead(props) {
    const { onSelectAllClick, order, orderBy, numSelected, rowCount, onRequestSort, headCells } = props;

    const [isOpenSearch, setIsOpenSearch] = useState({ id: null, open: false });

    const createSortHandler = (property) => (event) => {
        onRequestSort(event, property);
    };

    const handleIsOpenSearch = (id) => {
        setIsOpenSearch({ id, open: true });
    };

    const closeSearchInput = () => setIsOpenSearch({ id: null, open: false });

    return (
        <TableHead>
            <TableRow>
                <TableCell padding="checkbox" style={{ width: '50px' }}>
                    <Checkbox
                        color="primary"
                        indeterminate={numSelected > 0 && numSelected < rowCount}
                        checked={rowCount > 0 && numSelected === rowCount}
                        onChange={onSelectAllClick}
                        inputProps={{
                            'aria-label': 'select all desserts'
                        }}
                    />
                </TableCell>
                {headCells.map((headCell) => (
                    <TableCell
                        key={headCell.id}
                        align="center"
                        padding={headCell.disablePadding ? 'none' : 'normal'}
                        sortDirection={orderBy === headCell.id ? order : false}
                        sx={{ fontWeight: 'bold' }}
                    >
                        {isOpenSearch.open && isOpenSearch.id === headCell.id ? (
                            <Paper
                                component="div"
                                align="right"
                                sx={{
                                    zIndex: 1000,
                                    m: '0px 5px',
                                    display: 'flex',
                                    alignItems: 'center',
                                    width: '200',
                                    border: '1px solid #2196F3'
                                }}
                            >
                                <IconButton sx={{ p: '10px' }} aria-label="cancelPresentation" onClick={closeSearchInput}>
                                    <CancelPresentationIcon />
                                </IconButton>
                                <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
                                <InputBase
                                    sx={{ ml: 1, flex: 1 }}
                                    placeholder="Введите текст"
                                    inputProps={{ 'aria-label': 'search google maps' }}
                                />

                                <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
                                <IconButton sx={{ p: '10px' }} aria-label="search">
                                    <SearchIcon />
                                </IconButton>
                            </Paper>
                        ) : (
                            <div>
                                <IconButton
                                    sx={{ p: '10px' }}
                                    style={{ display: orderBy === headCell.id ? '' : 'none' }}
                                    aria-label="search"
                                    onClick={() => handleIsOpenSearch(headCell.id)}
                                >
                                    <SearchIcon />
                                </IconButton>
                                <TableSortLabel
                                    active={orderBy === headCell.id}
                                    direction={orderBy === headCell.id ? order : 'asc'}
                                    onClick={createSortHandler(headCell.id)}
                                >
                                    {headCell.label}
                                    {orderBy === headCell.id ? (
                                        <Box component="span" sx={visuallyHidden}>
                                            {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                                        </Box>
                                    ) : null}
                                </TableSortLabel>
                            </div>
                        )}
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}

export default EnhancedTableHead;
