import * as constants from './constants';

export const initialState = {
    isOpen: [],
    opened: true
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case constants.MENU_OPEN:
            return {
                ...state,
                isOpen: [action.id]
            };
        case constants.SET_MENU:
            return {
                ...state,
                opened: action.opened
            };
        default:
            return state;
    }
};

export default reducer;
